import React, { Suspense } from "react";
import { Router, Route } from "react-router-dom";
import createHistory from "history/createBrowserHistory";
import { Provider } from "react-redux";
import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension/developmentOnly";
import { syncHistoryWithStore, routerReducer, ConnectedRouter } from 'react-router-redux'
import rootreduser from "./redux/reducers/index";
import FormView from "./components/containers/FormView";
import Dashboard from "./components/containers/Dashboard";

const history = createHistory({
  basename: process.env.PUBLIC_URL
});

const store = createStore(
  rootreduser,
  composeWithDevTools(
    applyMiddleware(thunk),
    window.REDUX_DEVTOOLS_EXTENSION ? window.REDUX_DEVTOOLS_EXTENSION() : f => f
  )
);

const App = () => (
  <Provider store={store}>
    <Router 
    history={history}
    >
      <Suspense fallback={<div>...</div>}>
        <Route exact path="/" component={FormView} />
        <Route path="/success" component={Dashboard} />
      </Suspense>
    </Router>
  </Provider>
);

export default App;
